import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import PaymentComponent from './components/PaymentComponent';
import { Switch, Route } from 'react-router';
import ShoppingComponent from './components/ShoppingComponent';
import Dialogs from './container/dialogs/Index';
import dc from './constants/DomainConstants';

const styles: StyleRules<'root' > = {
  root: {
    top: 0,
    margin: 0,
    height: '100%',
    width: '100%',
    color: '#FFFFFF'
  },
};

type ClassNames = keyof typeof styles;

interface AppProps {
}

class App extends React.Component<AppProps & WithStyles<ClassNames>, {}> {
  render() {
    const {classes} = this.props;
    return (
      <div className={classes.root}>
        <Switch>
          <Route exact={true} path={dc.home} component={ShoppingComponent}/>
          <Route exact={true} path={dc.pay} component={PaymentComponent}/>
        </Switch>
        <Dialogs />
      </div>
    );
  }
}

export default withStyles<{}&ClassNames>(styles)<AppProps>(App);



